/* Reset and base styles  */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
}

/* Links */

a,
a:link,
a:visited {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

/* Common */

aside,
nav,
footer,
header,
section,
main {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

ul,
li {
	padding: 0;
  list-style: none;
}

img {
  vertical-align: top;
}

img,
svg {
  height: auto;
  max-width: 100%;
}

address {
  font-style: normal;
}

/* Form */

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;

}

input::-ms-clear {
  display: none;
}

button,
input {
	padding: 0;
  box-shadow: none;
  background-color: transparent;
  outline: transparent;
  border-color: transparent;
  font-family: inherit;
  font-size: inherit;
  line-height: 1;
  cursor: pointer;
}

input:focus,
input:active,
button:focus,
button:active,
textarea:focus {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}
