@use "sass:string";

@mixin font-face($font-name, $path, $weight: normal, $style: normal, $uni: '') {
	@font-face {
		font-family: string.quote($font-name);
		src:			
			url(#{$path}.woff2) format("woff2"),			
			url(#{$path}.ttf) format("truetype");
		font-weight: $weight;
		font-style: $style;
		@if $uni != '' {
			unicode-range: $uni;
		 }
	}
}

//@include mw1440 {}
@mixin mw1440(){
	@media (max-width: 1440px) {
		@content;
	}
}

